.warn {
  color: #FF8A3B;
  background-color: #FF8A3B;
}
.error {
  color: #EF6064;
  background-color: #EF6064;
}
.ok {
  color: #29D7B0;
  background-color: #29D7B0;
}
.panel-page {
  min-width: 1000px;
  min-height: 700px;
  background: #121b73 url(/static/media/bg@2x.03732052.png) no-repeat center;
  background-size: cover;
  color: #fff;
}
.panel-page .header {
  background: linear-gradient(to right, #0F48A8, #08227A);
  position: relative;
  text-align: center;
  color: #fff;
}
.panel-page .header .time {
  position: absolute;
  font-size: 16px;
  left: 50px;
  font-family: PingFangSC-Medium, PingFang SC;
  letter-spacing: 1px;
}
.panel-page .header .title {
  font-size: 30px;
  font-weight: bolder;
  width: 421px;
  margin: 0 auto;
  background: url(/static/media/biaoti@2x.93b5c187.png) no-repeat center;
  background-size: contain;
}
.panel-page .content {
  padding: 0 30px;
}
.panel-page .content .chartWrap {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.4);
  margin-top: 50px;
  position: relative;
}
.panel-page .content .chartWrap .section-head {
  position: absolute;
  top: -22px;
  left: 50%;
  height: 45px;
  line-height: 45px;
  width: 250px;
  margin-left: -125px;
  color: #fff;
  background: url(/static/media/biaoti_leixing@2x.4acf7dda.png) no-repeat center;
  background-size: contain;
  font-size: 14px;
  text-align: center;
  font-weight: bold;
}
.panel-page .content .center {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 50vh;
  padding: 30px 0;
  min-height: 300px;
  background-image: url(/static/media/gailan@2x.cd67ee38.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 200px;
}
.panel-page .content .center Row {
  flex: 1 1;
}
.panel-page .content .center-num {
  text-align: center;
  border-radius: 4px;
  border: 1px solid rgba(56, 192, 255, 0.7);
  background: rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 3px rgba(255, 255, 255, 0.2);
  padding: 10px;
}
.panel-page .content .center-num .num-text {
  font-size: 20px;
  color: #38C0FF;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
}
.panel-page .content .center-num .num-label {
  font-size: 12px;
  color: #fff;
}
.panel-page .content .life-legend {
  padding: 40px 40% 0;
  text-align: center;
  width: 100%;
}
.panel-page .content .life-legend .legend {
  width: 15px;
  height: 6px;
}
.panel-page .content .progress-wrap {
  text-align: center;
  font-size: 12px;
  margin-top: 20px;
}
.panel-page .content .progress-wrap .progress {
  width: 100%;
  position: relative;
  height: 10px;
  border-radius: 5px;
  background-color: #944A545D;
}
.panel-page .content .progress-wrap .progress .progess-bar {
  height: 100%;
  border-radius: 5px;
  display: flex;
  align-items: center;
  position: relative;
  left: -3px;
}
.panel-page .content .progress-wrap .progress .progess-bar::after {
  content: ' ';
  position: absolute;
  right: -15px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 6px solid #fff;
  background: #fff;
}
.panel-page .content .progress-wrap .progress .progess-bar.ok::after {
  border-color: #29D7B0;
}
.panel-page .content .progress-wrap .progress .progess-bar.warn::after {
  border-color: #FF8A3B;
}
.panel-page .content .progress-wrap .progress .progess-bar.error::after {
  border-color: #EF6064;
}
.panel-page .content .progress-wrap .progress .progess-bar span {
  flex: 1 1;
  color: #fff;
  font-size: 12;
}
.panel-page .content .progress-wrap .progress-legend {
  text-align: left;
}
.panel-page .content .progress-wrap .progress-legend .legend-icon {
  background: none;
  margin-left: 20px;
  margin-right: 5px;
}
.panel-page .content .life-footer {
  opacity: 0.5;
  padding: 20px 40px;
}
.panel-page .footer {
  background: none;
  text-align: center;
  color: #15DBF5;
}
